body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* CODE CONTAINER
*******************/
#twPlaygroundData{
  display: none;
}
.tw-loading{
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 100;
  background-color: rgba(0, 0, 0, 0.7);
  color: white;
  white-space: pre-line;
  text-align: center;
  font-size: 25px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.tw-code-container{
  position: relative;
  z-index: 90;
  color: #f8f8f1;
  background-color: #272722;
}

.tw-css-body{
  display: grid;
  grid-template-columns: -webkit-max-content 1fr;
  grid-template-columns: max-content 1fr;
}
.tw-code-container .tw-css-selector-container{
  padding: 5px 15px;
}
.tw-code-container .tw-css-selector-container:hover{
  background-color: #373732;
}
.tw-code-container .tw-css-selector{
  color: #9fd82e;
}

.tw-code-container .tw-punctuation{
  color: #f8f8f1;
}

.tw-code-container .tw-css-property{
  color: #f92572;
  padding-left: 15px;
  margin-right: 5px;
}

.tw-code-container .tw-css-value{
  position: relative;
  display: -webkit-flex;
  display: flex;
}

.tw-code-container .tw-css-controller-container{
  display: grid;
  grid-auto-flow: column;
  margin: 0 5px;
}

.tw-code-container .tw-label{
  cursor: pointer;
  margin-right: 5px;
}

.tw-code-container .tw-input{
  height: 19px;
  border: none;
  box-sizing: border-box;
  background-color: transparent;
  color: #f8f8f1;
  font-size: 16px;
  border-bottom: 1px dashed currentColor;
  outline: none;
}

/* HTML CONTAINER
*******************/

.htmlContainer{
  position: relative;
  z-index: 10;
  padding: 15px;
}

/* ANIMATIONS
*******************/

@-webkit-keyframes tw-anim-element-highlight {
  from {
    -webkit-filter: brightness(0.8);
            filter: brightness(0.8);
  }

  to {
    -webkit-filter: brightness(1.4);
            filter: brightness(1.4);
  }
}

@keyframes tw-anim-element-highlight {
  from {
    -webkit-filter: brightness(0.8);
            filter: brightness(0.8);
  }

  to {
    -webkit-filter: brightness(1.4);
            filter: brightness(1.4);
  }
}
